
<template>
  <div :class="{'inside': $route.name == 'Home'}">
      <b-container class="">
      <div class="wrap--white">
      <b-row class="header">
      <b-col>
      <a class="logo" href="/"><span class="first">COVID</span><span class="second">ПОКА</span></a>
      <router-link class="news-link" to="/news">Новости</router-link>
      </b-col>
      </b-row>
      <b-row>
      <b-col>
      <div class="select__wrap"  v-if="$route.name=='Home'">

      <b-form-select @change="changeSelectValue" v-model="selected" :options="options">

      </b-form-select>
      </div>
      </b-col>
      </b-row>
      </div>
      </b-container>
      <br>
    <div v-if="$route.name == 'Home'">
      <b-container>
        <b-row>
          <b-col>
            <div class="wrap--white">

              <yandex-map @map-was-initialized="initMap"
                          :coords="coords"
                          zoom="13"
                          style="width: 100%; height: 600px;"

                          :settings="settings"
                          :scroll-zoom="true"

                          @boundschange="boundsChange"

              >
                  <ymap-marker v-for="(item, index) in all"
                               marker-id="index"
                               :key="index"
                               :coords="[item.coord.lon, item.coord.lat]"
                               :icon="markerIcon"
                               hint-content="Hint content 1"
                               :balloon="{header: item.organization, body: item.organization, footer: item.organization}"
                               cluster-name="1"
                               @map-was-initialized="mapCreated"
                  ></ymap-marker>
              </yandex-map>
            </div>
          </b-col>
        </b-row>
      </b-container>
    </div>
      <router-view></router-view>

      <b-sidebar id="sidebar-1" title="Клиники поблизости" bg-variant="white" visible shadow  v-if="testData && $route.name == 'Home'">
      <div v-for="testDataItem in testData" class="wrap-card wrap--white hospital-item">
        <div v-if="1">
          <h3 class="hospital-item__title">{{testDataItem.organisation}}</h3>
          <p><strong>Город: </strong> {{testDataItem.city}}, {{testDataItem.address}}</p>
          <div class="hospital-item__info">
            <p>
              <strong>Анализы: </strong></p>
            <p v-for="test in checkOptions" :key="test">
              <span>{{test}}</span></p>

            <p><strong>Срок </strong> {{testDataItem.time_of_completion}}</p>
            <p><strong>Цена:</strong> {{testDataItem.pcr_test_price}}р</p>
            <p><a :href="testDataItem.url">Перейти на сайт</a></p>
        </div>

      </div>
      </div>
      </b-sidebar>
  </div>
</template>

<script>
import { yandexMap, ymapMarker, loadYmap } from 'vue-yandex-maps'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

export default {
  name: "Example",
  components:  { yandexMap, ymapMarker },
  data() {
    return {
      options: ["Выберите тип анализа", "ПЦР","Тест на антитела"],
      selected: "Выберите тип анализа",
      selectedId: '0',
      coords: [55.767303705804366,49.09997749999995],
      testData: {},
      markerIcon: {
        layout: 'default#imageWithContent',
        imageHref: 'http://37.18.100.248/clinic.png',
        imageSize: [50, 50],
        imageOffset: [0, 0],
        content: '',
        contentOffset: [0, 15],
        contentLayout: '<div style="background: red; width: 50px; color: #FFFFFF; font-weight: bold;"></div>'
      },
      optionsTypes: {
        pcr_test_price: "ПЦР",
        antibodies_test_price: "Тест на антитела",
      },
      map: null,
      settings: {
        apiKey: 'd174f9bc-b812-4548-95cc-71e0b5944348',
        zoom: 13,
        lang: 'ru_RU',
        coordorder: 'latlong',
        version: '2.1',
      },
      all: {

      },
      news: [
       ]
    };
  },
  computed: {
    checkOptions() {
      if(this.selected && this.selected!='Выберите тип анализа') {
        return [this.selected]
      } else {
        return [this.options[1], this.options[2]]
      }
    },
    newOptions() {

    }
  },
  async mounted() {
    await loadYmap({ ...this.settings, debug: true });
this.getAllObjects();
  this.getNews();
  },
  methods: {
    changeSelectValue() {
      this.selectedId = this.options.indexOf(this.selected);
    },
    getAllObjects() {
      this.$http
          .get('http://37.18.100.248/api/list_all', {
           })
          .then( (response) => {
            if(response.data.ok === true) {
              this.all = (response.data.places);
            }
          });

      this.$http
          .get('http://37.18.100.248/api/list', {
            params: {position: `55.767303705804366, 49.09997749999995`, distance: '5000' }})
          .then( (response) => {
            if(response.data.ok === true) {
              console.log((response.data))
              this.testData = (response.data.places);
            }
          });
    },
    getNews() {
      this.$http
          .get('http://testscovid.ru/api/news', )
          .then( (response) => {
            this.news = response.data.news
          });
    },
    initMap(e) {

      ymaps.geolocation.get({
        // Выставляем опцию для определения положения по ip
        provider: 'yandex',
        // Карта автоматически отцентрируется по положению пользователя.
        mapStateAutoApply: true
      }).then(function (result) {
        this.coords = (result.geoObjects.position);

      });
    },
    onClick(e) {
      this.coords = e.get('coords');
    },
    boundsChange(e) {

      let point1 = e.originalEvent.newBounds[0][0];
      let point2 = e.originalEvent.newBounds[0][1];
      let point3 = e.originalEvent.newBounds[1][0];
      let point4 = e.originalEvent.newBounds[1][1];
      this.$http
          .get('http://37.18.100.248/api/list', {

            params: {viewport: `${point1}, ${point2}, ${point3}, ${point4}`,
              analyse_type : this.selectedId
            }})
          .then( (response) => {
            if(response.data.ok === true) {
              console.log((response.data))
              this.testData = (response.data.places);
            }
          });
    },
    mapCreated(e) {
      console.log('created')
    },
    searchByCoords() {
      this.$http
          .get('http://37.18.100.248/api/list', {
            params: {viewport: '55.75549426836682,48.99436281488234,55.87148593494652,49.24464510247998'
            }})
          .then( (response) => {
            if(response.data.ok === true) {
              console.log((response.data))
              this.testData = (response.data.places);
            }
          });
    }
  },
};
</script>